import React from 'react';

const DetailShippingPayment = () => {
    return (
        <div className="col shipping-payment">
            <div className="content">
                <div className="description">
                    When you purchase something from our store, as part of the buying and selling process, we collect the personal information you give us such as your name, address and email address. <br/>
                    When you browse our store, we also automatically receive your computers internet protocol (IP) address in order to provide us with information that helps us learn about your browser and operating system.<br/>
                    Email marketing (if applicable): With your permission, we may send you emails about our store, new products and other updates.
                </div>
            </div>
        </div>
    );
};

export default DetailShippingPayment;